.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.main-body {
  margin-top: 0px; 
}
.main-container {
  margin: 0%;
}
.footer{
  font-size: 11px;
  background: #F5F9FC;
  height: 35px;
  color: #354052;
  padding: 10px 30px;
  bottom: 0px;
  position: fixed;
  width: 100%;
  text-align: right
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}