.topContainer {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-top: 80px;
}

.icon-image {
  height: 2.4em;
  width: 2.4em;
  object-fit: contain;
}

.login-bg-image {
  background-image: url('../styles/Images/niqbg.png');
  background-repeat: no-repeat;
  background-size: cover;
  height: 500pxpx;
  position: sticky;
  top:0;
  bottom: 0;
  right: 0;
  left: 0;
  z-index: -5;
  }

.article {
  height: 100%;
  position: sticky;
  overflow: hidden;
}

.image {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.header {
  font-size: 60px;
  color: white;
  text-align: start;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 60px;
  width: 50%;
  margin-top: 18%;
  font-weight: bold;
  margin-left: 5%;
  font-family: AktivGroteskExtendedBold;
}

.headerL {
  font-size: 50px;
  color: white;
  text-align: end;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: fit-content;
  margin: auto;
}

.container .box {
  display: flex;
  flex-direction: row;
}


.container .box .box-cell.box1 {
  background: green;
  color: white;
  text-align: justify;
}

.container .box .box-cell.box2 {
  background: lightgreen;
  text-align: justify
}

.login-floating {
  width: 400px;
  height: 300px;
  background-color: white;
  text-align: center;
  position: absolute;
  right: 0;
  top: 0;
  margin-top: 10%;
  margin-right: 2%;
}

.login-niq-logo {
  width: 180px;
  margin-left: 22px;
  margin-top: 30px;
  padding-bottom: 45px;
  margin-right: 20px;
}

.email-login {
  font-size: 16px;
  height: 40px;
  width:80%;
}
.text-login{
  font-size: 16px;
  font-weight: bold;
  text-align: start;
  width:80%;
  margin-left: 10%;
  margin-bottom: 2%;
  font-family :"Aktiv Grotesk",sans-serif;

}

.btn-login {
  background-color: #000000;
  color: #ffffff;
  font-weight: bold;
  font-size: 13px;
  height: 40px;
  width:80%;
  margin-top: 10%;
}
.btn-login:hover{
  background-color: #00FE00;
  color: #000000;

}
.maincommondiv{
  /* text-align: center; */
  display: flex;
  justify-content: center;

}
.landingpagemaindiv{
  width: 1000px;
  height: 100%;
  display: inline-block;
  text-align: center;
  margin-top: 80px;
  justify-content: center;
  margin-left: 10%;
}
.appmodulepanelLanding{
  display: inline-block!important;
  width: 80px;
  height: 100px;
  margin: 40px 80px;
  text-align: center;
  cursor: pointer;
  background-color: transparent;  
  border-radius: 10%;  
}
.module_name_label {
  text-align: center;
  font: bold normal 13px "Aktiv Grotesk","Montserrat Regular",Sans-Serif;
  letter-spacing: 0;
  color: #000;
  opacity: 1;
  overflow: hidden;
  margin-top: 10px;
}
.landingPageHeading{
  font-size: 17px;
  font-weight: bolder;
  color: #000000;
  margin-top: 20px;
}
.gridpanelImageLanding {
  background-color: #fff;
  width: calc(100% - 2.5rem);
  height: auto;
  border-radius: 4px;
  margin-top: 0;
  border: 5px solid #fff;
}
.imageDiv {
  box-shadow: 0 0 3px #666;
  width: 130px;
  height: 135px;
  background-color: #fff;
  padding: 7px 5px 7px 5px;
}